<template>
  <div>
    <navBedRoom/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Registro de Habitación</b>
      </h1>
      <h5 class="text-center text-dark"><b>Llene el siguiente formulario</b></h5>
      <div style="width:400px">
        <b-alert show variant="danger" style="width:400px" v-if="isCorrect!==null">
          <h4 class="alert-heading">{{avise}}</h4>
          <p>{{message}}</p>
        </b-alert>
      </div>
      <form @submit="save">
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="genero">Género</label>
            <select class="custom-select" id="genero" v-model.number="genero.id" required>
              <option disabled value>--Seleccione--</option>
              <option value="1">Masculino</option>
              <option value="2">Femenino</option>
            </select>
          </div>
          <div class="col-md-6">
            <label for="tipoHabitacion">Tipo de Habitación</label>
            <select
              class="custom-select"
              id="tipoHabitacion"
              v-model.number="tipoHabitacion.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Simple</option>
              <option value="2">Doble</option>
            </select>
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-4">
            <label for="tipoDocumento">Piso</label>
            <select class="custom-select" id="piso" v-model.number="piso.id" required>
              <option disabled value>--Seleccione--</option>
              <option value="1">Planta Baja</option>
              <option value="2">Segundo Piso</option>
              <option value="3">Tercer Piso</option>
              <option value="4">Cuarto Piso</option>
              <option value="5">Quinto Piso</option>
              <option value="6">Ultimo Piso</option>
            </select>
          </div>
          <div class="col-md-4">
            <label for="tipoDocumento">Estado Habitación</label>
            <select
              class="custom-select"
              id="estadoHabitacion"
              v-model.number="estadoHabitacion.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Disponible</option>
              <option value="2">Ocupada</option>
            </select>
          </div>
          <div class="col-md-4">
            <label for="numero">Número de Habitación</label>
            <input
              class="form-control"
              placeholder="Número de Habitación"
              v-model="numero"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <button class="btn btn-primary">Registrar</button>
        </div>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navBedRoom from "@/components/bedroom/navBedRoom.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "nuevaHabitacion",
  data() {
    return {
      genero: {
        id: null
      },
      piso: {
        id: null
      },
      estadoHabitacion: {
        id: null
      },
      tipoHabitacion: {
        id: null
      },
      numero: null,
      message: null,
      isCorrect: null,
      avise: null
    };
  },
  methods: {
    save(e) {
      e.preventDefault();
      this.avise = "";
      this.message = "";
      axios
        .post(`${URL}/api/habitacion`, {
          piso: this.piso,
          estadoHabitacion: this.estadoHabitacion,
          tipoHabitacion: this.tipoHabitacion,
          genero: this.genero,
          numero: this.numero
        })
        .then(response => {
          (this.piso = {}),
            (this.estadoHabitacion = {}),
            (this.tipoHabitacion = {}),
            (this.genero = {}),
            (this.numero = null),
            bootbox.alert({
              title: "Datos Guardados",
              message: response.data.mensaje,
              locale: "es",
              backdrop: true,
              callback: this.$router.push("/habitaciones")
            });
        })
        .catch(error => {
          this.avise = "Error";
          this.isCorrect = false;
          this.message = "Detalle: " + error.response.data.mensaje;
          bootbox.alert({
            title: "Error",
            message: "No se pudo registrar",
            locale: "es",
            backdrop: true
          });
          console.log("Error " + error.response.status + " Conflict");
        });
    }
  },
  components: {
    navBedRoom,
    Footer
  }
};
</script>

